<template>
  <div>
    <!-- <div id="firebaseui-auth-container"></div> -->
    <div @click="loginByGoogle">google</div>
    <div v-if="true">
      <div>api</div>
      <div>
        <input type="tel" v-model="phone" ref="phone" />
        <!-- <input v-model="phone" placeholder="" /> -->
      </div>
      <div id="phone-login-recaptcha"></div>
      <div>
        <input v-model="verifyCode" placeholder="" />
        <div @click="sendVerifyCode"></div>
      </div>
      <div @click="loginByPhone"></div>
    </div>
    <div v-if="false">
      <div>
        <div class="ipt">
          <input v-model="email" placeholder="" />
        </div>
        <div class="ipt">
          <input v-model="emailPass" placeholder="" />
        </div>
        <div @click="loginByEmail"></div>
      </div>
    </div>
    <div @click="logout" class="logout-button">logout</div>
  </div>
</template>

<script>
import firebase from "firebase/app";
import * as intlTelInput from "intl-tel-input";
import "intl-tel-input/build/css/intlTelInput.min.css";
// import * as firebaseui from "firebaseui";
// import "firebaseui/dist/firebaseui.css";

export default {
  data() {
    return {
      phone: "+8613619208941",
      verifyCode: "123456",
      recaptchaVerifier: null,
      confirmResult: null,
      email: "1233@qq.com",
      emailPass: "123456"
    };
  },
  watch: {
    phone() {
      console.log(this.iti.getSelectedCountryData());
    }
  },
  methods: {
    logout() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$toast("");
        })
        .catch(error => {
          console.error(error);
        });
    },
    sendVerifyCode() {
      console.log("");
      firebase
        .auth()
        .signInWithPhoneNumber(this.phone, this.recaptchaVerifier)
        .then(result => {
          this.confirmResult = result;
          console.log("send code:", result);
        })
        .catch(error => {
          console.log("send code error:", error);
        });
    },
    loginByPhone() {
      if (this.confirmResult) {
        this.confirmResult
          .confirm(this.verifyCode)
          .then(result => {
            // User signed in successfully.
            console.log(result);
          })
          .catch(function(error) {
            // User couldn't sign in (bad verification code?)
            console.log("loginByPhone error", error);
          });

        // const credential = firebase.auth.PhoneAuthProvider.credential(
        //   this.confirmResult.verificationId,
        //   this.verifyCode
        // );
        // firebase
        //   .auth()
        //   .signInWithCredential(credential)
        //   .then(res => {
        //     console.log(res);
        //   })
        //   .catch(error => {
        //     console.log(error);
        //   });
      }
    },
    loginByEmail() {
      firebase
        .auth()
        .signInWithEmailAndPassword(this.email, this.emailPass)
        .catch(error => {
          console.log(error);
          if (error.code === "auth/user-not-found") {
            this.createdUserByEmail();
          } else {
            this.$toast(error.message);
          }
        });
    },
    createdUserByEmail() {
      firebase
        .auth()
        .createUserWithEmailAndPassword(this.email, this.emailPass)
        .catch(error => {
          // console.log(error);
          this.$toast(error.message);
        });
    },
    loginByGoogle() {
      const provider = new firebase.auth.GoogleAuthProvider();
      firebase
        .auth()
        .signInWithPopup(provider)
        .then(function(result) {
          // This gives you a Google Access Token. You can use it to access the Google API.
          // const token = result.credential.accessToken;
          // The signed-in user info.
          // const user = result.user;
          console.log(result);
          // ...
        })
        .catch(function(error) {
          console.log("google login error:", error);
          // // Handle Errors here.
          // var errorCode = error.code;
          // var errorMessage = error.message;
          // // The email of the user's account used.
          // var email = error.email;
          // // The firebase.auth.AuthCredential type that was used.
          // var credential = error.credential;
          // // ...
        });
    },
    loginByTwitter() {
      const provider = new firebase.auth.TwitterAuthProvider();
      firebase
        .auth()
        .signInWithPopup(provider)
        .then(function(result) {
          // var token = result.credential.accessToken;
          // var secret = result.credential.secret;
          // var user = result.user;
          console.log(result);
        })
        .catch(function(error) {
          // var errorCode = error.code;
          // var errorMessage = error.message;
          // var email = error.email;
          // var credential = error.credential;
          console.error(error);
        });
    }
  },
  created() {
    firebase.auth().onAuthStateChanged(user => {
      console.log("user: ", user);
      if (user) {
        this.$toast("!");
      }
    });
  },
  mounted() {
    // 
    firebase.auth().useDeviceLanguage();
    firebase.auth().settings.appVerificationDisabledForTesting = true;
    this.recaptchaVerifier = new firebase.auth.RecaptchaVerifier(
      "phone-login-recaptcha",
      {
        size: "invisible",
        callback: function(response) {
          console.log("Recaptcha", response);
          // reCAPTCHA solved, allow signInWithPhoneNumber.
          // onSignInSubmit();
        }
      }
    );

    this.iti = intlTelInput(this.$refs.phone, {
      initialCountry: "cn",
      separateDialCode: true
    });
  },
  beforeDestroy() {
    // if (this.ui) {
    //   this.ui.reset();
    // }
  }
};
</script>
<style lang="less">
.logout-button {
  display: flex;
  justify-content: center;
  align-items: center;
}
.iti-mobile .iti--container {
  font-size: 12px;
}
</style>
